const uni = require('uni').default;




















import pageHead from '../../../components/page-head.vue'

export default {
	data() {
		return {
			title: 'request-payment',
			providerList: []
		}
	},
	onLoad: function () {

		uni.showModal({
			content:"小程序支付正在申请中...",
			showCancel:false
		})
		return;

		uni.getProvider({
			service: "payment",
			success: (e) => {
				console.log("payment success", e);
				this.providerList = e.provider.map((value) => {
					switch (value) {
						case 'alipay':
							return {
								name: '支付宝',
								id: value,
								loading: false
							}
						case 'wxpay':
							return {
								name: '微信',
								id: value,
								loading: false
							}
					}
				})
			},
			fail: (e) => {
				console.log("获取登录通道失败", e);
			}
		});
	},
	methods: {
		async requestPayment(e, index) {
			this.providerList[index].loading = true;
			let orderInfo = await this.getOrderInfo(e.id);
			console.log("得到订单信息", orderInfo);
			if (orderInfo.statusCode !== 200) {
				console.log("获得订单信息失败", orderInfo);
				uni.showModal({
					content:"获得订单信息失败",
					showCancel:false
				})
				return;
			}
			uni.requestPayment({
				provider: e.id,
				orderInfo: orderInfo.data,
				success: (e) => {
					console.log("success", e);
					uni.showToast({
						title: "感谢您的赞助!"
					})
				},
				fail: (e) => {
					console.log("fail", e);
					uni.showModal({
						content:"支付失败,原因为: " + e.errMsg,
						showCancel:false
					})
				},
				complete: () => {
					this.providerList[index].loading = false;
				}
			})
		},
		getOrderInfo(e) {
			let appid = uni.os.plus ? plus.runtime.appid : "";

			let url = 'https://demo.dcloud.net.cn/payment/?payid=' + e + '&appid=' + appid + '&total=0.01';
			return new Promise((res) => {
				uni.request({
					url: url,
					success: (result) => {
						res(result);
					},
					fail: (e) => {
						res(e);
					}
				})
			})
		}
	},
	components: {
		pageHead
	}
}



